/**
 * Styles for the mobile search block
 */
.mobile-search {
    // Selector styles
    align-items: center;
    display: flex;
    padding: 0 0.625em;

    // @media queries
    @include breakpoint-up(lg) {
        display: none;
    }

    // Pseudo-classes and pseudo-elements
    // TODO: add code

    // Nested selectors
    // TODO: add code

    // BEM elements
    &__input {
        display: block;
        flex: 1;
        padding: 1.825em 0;

        color: $color-primary;
        font-size: 0.75rem;
        font-weight: $font-weight-bold;
        letter-spacing: .2em;

        background: transparent;
        border: none;
        outline: none;

        &::placeholder {
            color: $color-silver-sand;
        }
    }

    &__submit {
        display: block;
        flex: 0 0 auto;
        padding: 0.5em 0.25em;

        background: transparent;
        border: none;
        cursor: pointer;
        outline: none;

        transition: all 0.25s ease;

        &:hover {
            color: $color-secondary;
        }

        .focus-utility &:focus {
            box-shadow: 0 0 0 0.25em rgba($color-focus, 0.5);
        }
    }

    // BEM modifiers
    // TODO: add code
}