/**
 * Styles for the search block
 */
.search {
    // Selector styles
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 1;
    
    height: 6.25em;

    background: $color-primary;
    opacity: 0;

    transition: opacity 0.25s ease,
                visibility 0.25s ease 0.25s;
    visibility: hidden;

    // @media queries
    // TODO: add code

    // Pseudo-classes and pseudo-elements
    // TODO: add code

    // Nested selectors
    // TODO: add code

    // BEM elements
    &__form {
        align-items: center;
        display: flex;
        height: 100%;
        margin: 0 auto;
        max-width: 75em;
        padding: 0 0.625em;
    }

    &__input {
        flex: 1 1;

        color: $color-concrete;
        font-family: $font-family-regular;
        font-size: 0.75rem;
        letter-spacing: 0.2em;

        background: transparent;
        border: none;
        outline: none;

        &::placeholder {
            color: $color-silver-sand;
            text-transform: uppercase;
        }
    }

    &__submit {
        color: $color-white;
        font-size: 1rem;

        background: transparent;
        border: none;
        cursor: pointer;
        opacity: 0.7;
        outline: none;

        transition: opacity 0.25s ease;

        &:hover,
        &:focus {
            opacity: 1;
        }

        .focus-utility &:focus {
            box-shadow: 0 0 0 0.2em rgba($color-white, 0.5);
        }
    }

    &__close {
        position: relative;

        display: block;
        height: 1.375em;
        margin-left: 1.25em;
        padding: 0.175em;
        width: 1.375em;

        color: $color-white;
        text-indent: -100em;

        background: transparent;
        border: none;
        cursor: pointer;
        opacity: 0.7;
        outline: none;

        transition: opacity 0.25s ease;

        &:before,
        &:after {
            content: "";

            left: 0;
            top: 50%;
            position: absolute;

            display: block;
            margin-top: -0.125em;
            height: 1px;
            width: 1.375em;

            background: $color-white;
        }

        &:before {
            transform: rotate(45deg);
        }

        &:after {
            transform: rotate(-45deg);
        }

        &:hover,
        &:focus {
            opacity: 1;
        }

        .focus-utility &:focus {
            box-shadow: 0 0 0 0.2em rgba($color-white, 0.5);
        }
    }

    // BEM modifiers
    &_visible {
        opacity: 1;

        transition-delay: 0s;
        visibility: visible;
    }
}
