/**
 * Styles for the rich text block
 */
.rich-text {
    // Selector styles
    color: $color-tundora;
    font-size: 0.75rem;
    line-height: 2.5;
    letter-spacing: .15em;

    // @media queries
    // TODO: add code

    // Nested selectors
    * + * {
        margin-top: 2em;
    }

    h3 {
        color: $color-black;
        font-size: 1.25em;
        font-weight: $font-weight-bold;
        text-transform: uppercase;
    }

    h3 + p {
        margin-top: 1em;
    }

    blockquote {
        padding-left: 1.5em;

        color: $color-black;
        font-size: 1.1667em;

        border-left: 3px solid $color-athens-gray;
    }

    // BEM elements
    // TODO: add code

    // BEM modifiers
    &_small-size {
        font-size: 0.6875rem;
    }

    &_small-spacing {
        line-height: 2;
    }
}
