/**
 * Styles for the specials block
 */
.specials {
    // Selector styles
    padding: 4.375em 0;
    background: $color-alabaster;

    // @media queries
    @include breakpoint-up(lg) {
        padding-bottom: 6.25em;
        padding-top: 6.25em;
    }

    // Pseudo-classes and pseudo-elements
    // TODO: add code

    // Nested selectors
    // TODO: add code

    // BEM elements
    &__inner {
        margin: 0 auto;
        max-width: 65em;
        padding: 0 0.625em;

        @include breakpoint-up(lg) {
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }

    &__heading {
        text-align: center;

        @include breakpoint-up(lg) {
            flex: 0 0 auto;
        }
    }

    &__slider {
        margin-top: 1.875em;

        @include breakpoint-up(md) {
            max-width: 48.75em;
            margin-left: auto;
            margin-right: auto;
        }

        @include breakpoint-up(lg) {
            flex: 1 1 auto;
            margin-left: 3.75em;
            margin-right: 0;
            margin-top: 0;
        }
    }

    // BEM modifiers
    // TODO: add code
}
