.select__origin {
  position: absolute;
  height: 1px;
  margin: -1px;
  padding: 0;
  width: 1px;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.select__button {
  display: block;
  padding: 0.4em 0;
  width: 100%;
  color: #1e2633;
  font-family: inherit;
  font-size: 0.9375rem;
  font-weight: 700;
